.catalog-section{padding:30px 0}.section-title{font-size:24px;font-weight:600;margin-bottom:20px;text-align:center}.catalog-tabs{display:flex;justify-content:center;margin-bottom:25px;gap:10px;position:relative}.catalog-tabs::after{content:"";position:absolute;bottom:-8px;left:50%;transform:translateX(-50%);width:80px;height:2px;background-color:#e0e0e0}.catalog-tab{padding:8px 30px;border-radius:30px;background-color:#f5f5f5;border:2px solid rgba(0,0,0,0);font-weight:600;font-size:16px;cursor:pointer;transition:all .25s ease;min-width:120px;text-align:center}.catalog-tab:hover{background-color:#e9e9e9}.catalog-tab.active{background-color:#000;color:#fff;border-color:#000;box-shadow:0 4px 8px rgba(0,0,0,.1)}@media(max-width: 576px){.catalog-tab{padding:6px 20px;font-size:14px;min-width:100px}}.catalog-slider{position:relative;transition:opacity .3s ease}.catalog-slider.fade-out{opacity:0}.catalog-slider :global(.swiper-button-next),.catalog-slider :global(.swiper-button-prev){color:#000;background-color:hsla(0,0%,100%,.8);width:40px;height:40px;border-radius:50%;box-shadow:0 2px 5px rgba(0,0,0,.1)}.catalog-slider :global(.swiper-button-next):after,.catalog-slider :global(.swiper-button-prev):after{font-size:18px}.catalog-slider :global(.swiper-button-disabled){opacity:.3}.swiper-container{margin:0;padding:10px 0;overflow:visible}.catalog-item{width:auto;transition:transform .3s ease}.catalog-item:hover{transform:translateY(-5px)}.catalog-item:hover .catalog-image{transform:scale(1.05)}.catalog-link{display:block;text-decoration:none;color:inherit}.catalog-image-wrapper{position:relative;overflow:hidden;border-radius:8px;aspect-ratio:3/4;margin-bottom:10px}.catalog-image{width:100%;height:100%;-o-object-fit:cover;object-fit:cover;transition:transform .3s ease}.catalog-title{font-size:16px;font-weight:500;text-align:center;margin-top:10px}@media(max-width: 768px){.catalog-title{font-size:14px}.section-title{font-size:20px;margin-bottom:15px}.catalog-section{padding:20px 0}.catalog-tabs{margin-bottom:20px}}@media(max-width: 480px){.section-title{font-size:18px}.catalog-tabs{margin-bottom:15px}:global(.swiper-button-next),:global(.swiper-button-prev){display:none}}@media(max-width: 400px){.section-title{font-size:16px;margin-bottom:12px}.catalog-title{font-size:13px}.catalog-section{padding:15px 0}.catalog-image-wrapper{margin-bottom:5px}}